"use client";

import type React from "react";

import { useState } from "react";
import Link from "next/link";
import { useRouter } from "next/navigation";
import { Header } from "@/components/header";
import { Footer } from "@/components/footer";
import { ArrowRight, Loader2, Eye, EyeOff, CheckCircle } from "lucide-react";
import { useAuth } from "@/lib/auth-context";

export default function AuthPage() {
  const [isLogin, setIsLogin] = useState(true);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState("");
  const [successMessage, setSuccessMessage] = useState("");

  const [formData, setFormData] = useState({
    nome: "",
    sobrenome: "",
    celular: "",
    email: "",
    senha: "",
    confirmSenha: "",
  });

  // estados para mostrar/ocultar senhas
  const [showLoginPassword, setShowLoginPassword] = useState(false);
  const [showSignupPassword, setShowSignupPassword] = useState(false);
  const [showSignupConfirmPassword, setShowSignupConfirmPassword] =
    useState(false);

  const { login, register } = useAuth();
  const router = useRouter();

  // formata para exibir no input: (22) 99754-0815
  const formatPhone = (value: string): string => {
    const digits = value.replace(/\D/g, "").slice(0, 11); // máximo 11 dígitos

    if (!digits) return "";

    if (digits.length <= 2) {
      return `(${digits}`;
    }

    if (digits.length <= 6) {
      return `(${digits.slice(0, 2)}) ${digits.slice(2)}`;
    }

    if (digits.length <= 10) {
      return `(${digits.slice(0, 2)}) ${digits.slice(2, 6)}-${digits.slice(
        6
      )}`;
    }

    return `(${digits.slice(0, 2)}) ${digits.slice(2, 7)}-${digits.slice(
      7,
      11
    )}`;
  };

  // remove máscara para enviar ao backend
  const unmaskPhone = (value: string): string => value.replace(/\D/g, "");

  // normaliza identifier do login: se tiver @ é email, se não, envia só dígitos
  const normalizeIdentifier = (value: string): string => {
    const trimmed = value.trim();
    if (!trimmed) return trimmed;

    if (trimmed.includes("@")) {
      // e-mail
      return trimmed;
    }

    // telefone
    const digits = trimmed.replace(/\D/g, "");
    return digits || trimmed;
  };

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;

    let newValue = value;

    if (name === "celular") {
      newValue = formatPhone(value);
    }

    setFormData((prev) => ({
      ...prev,
      [name]: newValue,
    }));

    setError("");
    setSuccessMessage("");
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsLoading(true);
    setError("");
    setSuccessMessage("");

    try {
      if (isLogin) {
        // LOGIN → usa identifier = email OU telefone
        const rawIdentifier = formData.email || formData.celular;
        const identifier = normalizeIdentifier(rawIdentifier);

        const result = await login(identifier, formData.senha);

        if (result.success) {
          router.push("/");
        } else {
          setError(result.error || "Erro ao fazer login.");
        }
      } else {
        // CADASTRO
        if (formData.senha !== formData.confirmSenha) {
          setError("Senha e confirmação de senha não conferem.");
          setIsLoading(false);
          return;
        }

        const result = await register({
          firstName: formData.nome,
          lastName: formData.sobrenome,
          email: formData.email,
          password: formData.senha,
          confirmPassword: formData.confirmSenha,
          // envia apenas dígitos para o backend
          phone: unmaskPhone(formData.celular) || undefined,
        });

        if (result.success) {
          // NÃO loga automaticamente: apenas mostra mensagem e troca para Login depois de 2.5s
          setSuccessMessage(
            "Conta criada com sucesso! Redirecionando para o login..."
          );

          // limpa senhas para segurança
          setFormData((prev) => ({
            ...prev,
            senha: "",
            confirmSenha: "",
          }));

          setTimeout(() => {
            setIsLogin(true);
            setSuccessMessage("");
          }, 2500);
        } else {
          setError(result.error || "Erro ao criar conta.");
        }
      }
    } finally {
      setIsLoading(false);
    }
  };

  // clique do Google → vai para /google (page.tsx que inicia o fluxo com a API)
  const handleGoogleLogin = () => {
    router.push("/google/callback");
  };

  return (
    <div className="min-h-screen flex flex-col">
      <Header />

      <div className="flex-1 flex items-center justify-center py-12 px-4">
        <div className="w-full max-w-md">
          {/* Demo Credentials Box (se quiser, pode remover agora que está real) */}
          <div className="mb-8 p-4 bg-accent/10 border border-accent/30 rounded-lg">
            <p className="text-sm font-medium text-foreground mb-2">
              Credenciais de Teste:
            </p>
            <div className="text-xs text-foreground/70 space-y-1">
              <p>
                <strong>Admin:</strong> admin@memoriza.com / admin123
              </p>
              <p>
                <strong>Usuário:</strong> maria@email.com / user123
              </p>
            </div>
          </div>

          {/* Toggle */}
          <div className="flex gap-4 mb-4">
            <button
              onClick={() => {
                setIsLogin(true);
                setError("");
                setSuccessMessage("");
              }}
              className={`flex-1 py-3 px-4 rounded-lg font-medium transition-all ${
                isLogin
                  ? "bg-primary text-primary-foreground"
                  : "bg-muted text-foreground hover:bg-muted/80"
              }`}
            >
              Login
            </button>
            <button
              onClick={() => {
                setIsLogin(false);
                setError("");
                setSuccessMessage("");
              }}
              className={`flex-1 py-3 px-4 rounded-lg font-medium transition-all ${
                !isLogin
                  ? "bg-primary text-primary-foreground"
                  : "bg-muted text-foreground hover:bg-muted/80"
              }`}
            >
              Cadastro
            </button>
          </div>

          {/* Sucesso */}
          {successMessage && (
            <div className="mb-4 p-3 bg-emerald-100 border border-emerald-300 text-emerald-800 rounded-lg text-sm flex items-center gap-2">
              <CheckCircle className="w-4 h-4" />
              <span>{successMessage}</span>
            </div>
          )}

          {/* Erro */}
          {error && (
            <div className="mb-4 p-3 bg-red-100 border border-red-300 text-red-700 rounded-lg text-sm">
              {error}
            </div>
          )}

          {/* Form */}
          <form onSubmit={handleSubmit} className="space-y-4">
            {isLogin ? (
              <>
                {/* Login Form */}
                <div className="space-y-4">
                  <div>
                    <label className="block text-sm font-medium text-foreground mb-2">
                      E-mail ou Celular
                    </label>
                    <input
                      type="text"
                      name="email"
                      placeholder="seu@email.com ou (99) 99999-9999"
                      value={formData.email}
                      onChange={handleInputChange}
                      className="w-full px-4 py-3 rounded-lg border border-border bg-background text-foreground placeholder:text-foreground/50 focus:outline-none focus:ring-2 focus:ring-accent"
                    />
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-foreground mb-2">
                      Senha
                    </label>
                    <div className="relative">
                      <input
                        type={showLoginPassword ? "text" : "password"}
                        name="senha"
                        placeholder="••••••••"
                        value={formData.senha}
                        onChange={handleInputChange}
                        className="w-full px-4 pr-10 py-3 rounded-lg border border-border bg-background text-foreground placeholder:text-foreground/50 focus:outline-none focus:ring-2 focus:ring-accent"
                      />
                      <button
                        type="button"
                        onClick={() =>
                          setShowLoginPassword((prev) => !prev)
                        }
                        className="absolute inset-y-0 right-0 flex items-center pr-3 text-foreground/60 hover:text-foreground/90"
                        aria-label={
                          showLoginPassword ? "Ocultar senha" : "Mostrar senha"
                        }
                      >
                        {showLoginPassword ? (
                          <EyeOff className="w-4 h-4" />
                        ) : (
                          <Eye className="w-4 h-4" />
                        )}
                      </button>
                    </div>
                  </div>

                  <div className="text-right">
                    <Link
                      href="/auth/forgot-password"
                      className="text-sm text-accent hover:text-accent/80 transition-colors font-medium"
                    >
                      Esqueceu a senha?
                    </Link>
                  </div>
                </div>
              </>
            ) : (
              <>
                {/* Signup Form */}
                <div className="grid grid-cols-2 gap-3">
                  <div>
                    <label className="block text-sm font-medium text-foreground mb-2">
                      Nome
                    </label>
                    <input
                      type="text"
                      name="nome"
                      placeholder="João"
                      value={formData.nome}
                      onChange={handleInputChange}
                      className="w-full px-4 py-3 rounded-lg border border-border bg-background text-foreground placeholder:text-foreground/50 focus:outline-none focus:ring-2 focus:ring-accent"
                    />
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-foreground mb-2">
                      Sobrenome
                    </label>
                    <input
                      type="text"
                      name="sobrenome"
                      placeholder="Silva"
                      value={formData.sobrenome}
                      onChange={handleInputChange}
                      className="w-full px-4 py-3 rounded-lg border border-border bg-background text-foreground placeholder:text-foreground/50 focus:outline-none focus:ring-2 focus:ring-accent"
                    />
                  </div>
                </div>

                <div>
                  <label className="block text-sm font-medium text-foreground mb-2">
                    Celular
                  </label>
                  <input
                    type="tel"
                    name="celular"
                    placeholder="(22) 99754-0815"
                    value={formData.celular}
                    onChange={handleInputChange}
                    className="w-full px-4 py-3 rounded-lg border border-border bg-background text-foreground placeholder:text-foreground/50 focus:outline-none focus:ring-2 focus:ring-accent"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-foreground mb-2">
                    E-mail
                  </label>
                  <input
                    type="email"
                    name="email"
                    placeholder="seu@email.com"
                    value={formData.email}
                    onChange={handleInputChange}
                    className="w-full px-4 py-3 rounded-lg border border-border bg-background text-foreground placeholder:text-foreground/50 focus:outline-none focus:ring-2 focus:ring-accent"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-foreground mb-2">
                    Senha
                  </label>
                  <div className="relative">
                    <input
                      type={showSignupPassword ? "text" : "password"}
                      name="senha"
                      placeholder="••••••••"
                      value={formData.senha}
                      onChange={handleInputChange}
                      className="w-full px-4 pr-10 py-3 rounded-lg border border-border bg-background text-foreground placeholder:text-foreground/50 focus:outline-none focus:ring-2 focus:ring-accent"
                    />
                    <button
                      type="button"
                      onClick={() =>
                        setShowSignupPassword((prev) => !prev)
                      }
                      className="absolute inset-y-0 right-0 flex items-center pr-3 text-foreground/60 hover:text-foreground/90"
                      aria-label={
                        showSignupPassword ? "Ocultar senha" : "Mostrar senha"
                      }
                    >
                      {showSignupPassword ? (
                        <EyeOff className="w-4 h-4" />
                      ) : (
                        <Eye className="w-4 h-4" />
                      )}
                    </button>
                  </div>
                </div>

                <div>
                  <label className="block text-sm font-medium text-foreground mb-2">
                    Confirmar Senha
                  </label>
                  <div className="relative">
                    <input
                      type={showSignupConfirmPassword ? "text" : "password"}
                      name="confirmSenha"
                      placeholder="••••••••"
                      value={formData.confirmSenha}
                      onChange={handleInputChange}
                      className="w-full px-4 pr-10 py-3 rounded-lg border border-border bg-background text-foreground placeholder:text-foreground/50 focus:outline-none focus:ring-2 focus:ring-accent"
                    />
                    <button
                      type="button"
                      onClick={() =>
                        setShowSignupConfirmPassword((prev) => !prev)
                      }
                      className="absolute inset-y-0 right-0 flex items-center pr-3 text-foreground/60 hover:text-foreground/90"
                      aria-label={
                        showSignupConfirmPassword
                          ? "Ocultar senha"
                          : "Mostrar senha"
                      }
                    >
                      {showSignupConfirmPassword ? (
                        <EyeOff className="w-4 h-4" />
                      ) : (
                        <Eye className="w-4 h-4" />
                      )}
                    </button>
                  </div>
                </div>

                <div className="flex items-start gap-2 pt-2">
                  <input
                    type="checkbox"
                    id="terms"
                    className="mt-1 accent-accent"
                  />
                  <label
                    htmlFor="terms"
                    className="text-sm text-foreground/70"
                  >
                    Concordo com os{" "}
                    <Link
                      href="/terms"
                      className="text-accent hover:text-accent/80 font-medium"
                    >
                      Termos de Serviço
                    </Link>
                  </label>
                </div>
              </>
            )}

            {/* Submit Button */}
            <button
              type="submit"
              disabled={isLoading}
              className="w-full mt-8 bg-primary text-primary-foreground py-3 rounded-lg font-medium hover:bg-primary/90 transition-colors flex items-center justify-center space-x-2 disabled:opacity-50"
            >
              {isLoading ? (
                <Loader2 size={18} className="animate-spin" />
              ) : (
                <>
                  <span>{isLogin ? "Entrar" : "Criar Conta"}</span>
                  <ArrowRight size={18} />
                </>
              )}
            </button>
          </form>

          {/* Divider */}
          <div className="relative my-8">
            <div className="absolute inset-0 flex items-center">
              <div className="w-full border-t border-border"></div>
            </div>
            <div className="relative flex justify-center text-sm">
              <span className="px-2 bg-background text-foreground/50">
                Ou continue com
              </span>
            </div>
          </div>

          {/* Social Login → Google */}
          <button
            type="button"
            onClick={handleGoogleLogin}
            className="w-full border-2 border-border bg-background text-foreground py-3 rounded-lg font-medium hover:border-accent hover:text-accent transition-all flex items-center justify-center space-x-2"
          >
            <svg
              className="w-5 h-5"
              viewBox="0 0 24 24"
              fill="currentColor"
            >
              <path d="M22.56 12.25c0-.78-.07-1.53-.2-2.25H12v4.26h5.92c-.26 1.37-1.04 2.53-2.21 3.31v2.77h3.57c2.08-1.92 3.28-4.74 3.28-8.09z" />
              <path d="M12 23c2.97 0 5.46-.98 7.28-2.66l-3.57-2.77c-.98.66-2.23 1.06-3.71 1.06-2.86 0-5.29-1.93-6.16-4.53H2.18v2.84C3.99 20.53 7.7 23 12 23z" />
              <path d="M5.84 14.09c-.22-.66-.35-1.36-.35-2.09s.13-1.43.35-2.09V7.07H2.18C1.43 8.55 1 10.22 1 12s.43 3.45 1.18 4.93l2.85-2.22.81-.62z" />
              <path d="M12 5.38c1.62 0 3.06.56 4.21 1.64l3.15-3.15C17.45 2.09 14.97 1 12 1 7.7 1 3.99 3.47 2.18 7.07l3.66 2.84c.87-2.6 3.3-4.53 6.16-4.53z" />
            </svg>
            <span>Google</span>
          </button>

          {/* Toggle Message */}
          <p className="text-center text-sm text-foreground/70 mt-6">
            {isLogin ? "Não tem uma conta? " : "Já tem uma conta? "}
            <button
              onClick={() => {
                setIsLogin(!isLogin);
                setError("");
                setSuccessMessage("");
              }}
              className="text-accent hover:text-accent/80 font-medium transition-colors"
            >
              {isLogin ? "Cadastre-se" : "Faça login"}
            </button>
          </p>
        </div>
      </div>

      <Footer />
    </div>
  );
}